:root{--primary-color:#0056d2;--secondary-color:red;--screen-sm:640px;--screen-md:768px;--screen-lg:1024px;--screen-xl:1280px;--screen-2xl:1536px}*,*:before,*:after{box-sizing:border-box}html,body{font-family:"Open Sans",sans-serif;font-size:16px;color:#000;max-width:100vw;overflow-x:hidden;padding:0;margin:0}.App{height:100vh;display:grid;max-width:100vw;overflow-x:hidden;grid-template-rows:auto 1fr auto}img{max-width:100%}a{text-decoration:none;color:inherit}li{list-style:none}.header{margin:0 auto}.container{max-width:1280px;margin:0 auto;padding:25px 45px}.w-full{width:100vw;overflow-x:hidden}.w-max-full{width-max:100%}.w-xl{max-width:1280px}.header,.flex-row{display:flex;align-items:center;flex-wrap:wrap;justify-content:space-between;grid-gap:15px;gap:15px}.flex-wrap{flex-wrap:wrap}.flex-nowrap{flex-wrap:nowrap}.flex-grow{flex-grow:1}.flex-shrink{flex-shrink:1}.justify-start{justify-content:flex-start}.justify-end{justify-content:flex-end}@media(max-width: 640px){.collapse-sm{display:none}}@media(max-width: 768px){.collapse-md{display:none}}@media(max-width: 1024px){.collapse-lg{display:none}}@media(max-width: 1280px){.collapse-xl{display:none}}@media(min-width: 640px){.display-sm{display:none}}@media(min-width: 768px){.display-md{display:none}}@media(min-width: 1024px){.display-lg{display:none}}@media(min-width: 1280px){.display-xl{display:none}}.header{flex-wrap:nowrap;font-size:14px;padding:8px 25px;min-height:64px;border-bottom:1px solid #e0e0e0}.header__logo{min-width:118px;max-width:128px}@media(min-width: 768px){.header__logo{order:-1}}.header .flex-row{flex-wrap:nowrap}.search-bar{max-width:550px;flex-wrap:nowrap}.search-bar__field{flex-grow:1}footer{background-color:red;bottom:0}.btn{display:inline-block;border-radius:7px;margin:.25em;cursor:pointer}.btn--primary{background-color:blue;border:3px solid blue;color:#fff}.btn--outline-primary{background-color:inherit;border:3px solid blue}.btn--lg{padding:.5em 1.2em}
